export function getErrorMessage(error) {
  if (error.graphQLErrors) {
    for (const graphQLError of error.graphQLErrors) {
      if (
        graphQLError.extensions &&
        graphQLError.extensions.code === "BAD_USER_INPUT"
      ) {
        return graphQLError.message;
      }
    }
  }
  switch (error.message) {
    case `ketQua validation failed: ketQua: Cast to Array failed for value "Bn  t n gii hn truy cp." at path "ketQua"`:
      return `Bn khng  lt truy cp, lin h  nhn thm`;
    case `Response not successful: Received status code 400`:
      return `D liu nhp vo khng hp l.`;
    default:
      return error.message;
  }
}
